body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
  padding: 10px 20px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
  background-color: var(--color-primary);
  text-decoration: none;
  transition: 0.3s;
}
.button:hover {
  background-color: #173598;
}
.button__text {
  color: #fff;
  font-size: 13px;
  margin-left: 10px;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.7)),
    url(/static/media/fondo.8784cb96.jpg);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__container-logo {
  text-align: right;
  padding: 20px;
}
.header__logo {
  height: 80px;
}
.header__container-title {
  text-align: left;
  color: #fff;
  width: 50%;
}
.header__title {
  font-size: 80px;
  margin-bottom: 25px;
}
.header__description {
  font-size: 18px;
  line-height: 30px;
  border-left: 3px solid #fff;
  padding: 0 15px;
  margin-bottom: 25px;
}

@media (max-width: 905px) {
  .header__title {
    font-size: 70px;
  }
  .header__description {
    font-size: 16px;
    line-height: 20px;
  }
}

@media (max-width: 700px) {
  .header__logo {
    height: 51px;
  }
  .header__container-title {
    width: 70%;
  }
}

@media (max-width: 530px) {
  .header__title {
    font-size: 60px;
  }
  .header__container-title {
    width: 100%;
  }
}

.about {
  margin-top: 70px;
}
.about__container {
  padding: 40px;
  display: flex;
  justify-content: space-between;
}
.about__title {
  margin-right: 40px;
}
.title__text {
  font-size: 40px;
  color: var(--color-text);
  padding: 10px;
  display: inline-block;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border-bottom: 5px solid var(--color-primary);
}
.about__description {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

@media (max-width: 700px) {
  .about__container {
    display: block;
    padding: 0;
  }
  .title__text {
    font-size: 25px;
  }
  .about__title {
    margin-bottom: 30px;
    margin-right: 0;
    text-align: center;
  }
  .about__description {
    font-size: 16px;
  }
}

.card {
  padding: 20px;
}
.card__container-img {
  margin-bottom: 35px;
}
.card__img {
  width: 80%;
  border-radius: 10px;
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.175) !important;
}
.card__title {
  font-size: 30px;
  color: var(--color-text);
  margin-bottom: 15px;
  letter-spacing: 1px;
}
.card__description {
  font-size: 15px;
  letter-spacing: 1px;
  color: var(--color-text);
}

@media (max-width: 700px) {
  .card__container-img {
    margin-bottom: 10px;
  }
  .card__img {
    width: 100%;
    box-shadow: 0 0.2rem 0.7rem rgba(0, 0, 0, 0.175) !important;
  }
  .card__title {
    font-size: 20px;
  }
}

@media (max-width: 530px) {
  .card {
    padding: 0;
  }
}

.porfolio {
  margin-top: 50px;
  padding: 40px 0;
  background: #f8f8f88f;
}
.title-center {
  text-align: center;
  margin-bottom: 70px;
}
.title__description {
  font-size: 17px;
  margin-top: 25px;
  letter-spacing: 1px;
}
.porfolio__container-card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 15px;
}

@media (max-width: 700px) {
  .porfolio__container-card {
    grid-template-columns: 1fr;
    grid-gap: 60px;
  }
}

.contact {
  margin-top: 50px;
}
.contact__container {
  padding: 40px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 60px;
}
.title__description--margin {
  margin-top: 30px !important;
}
.contact__content {
  justify-self: center;
  align-self: center;
  width: 80%;
}
.boton {
  padding: 15px 30px;
  border-radius: 10px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 18px;
  text-decoration: none;
  transition: 0.3s;
}
.boton--green {
  background-color: rgb(46, 204, 113);
  border-bottom: 5px solid rgb(35, 185, 99);
}
.boton--green:hover {
  background-color: rgb(35, 185, 99);
}
.boton--red {
  background-color: rgb(231, 76, 60);
  border-bottom: 5px solid rgb(220, 64, 51);
}
.boton--red:hover {
  background-color: rgb(220, 64, 51);
}
.boton__text {
  margin-left: 30px;
}

@media (max-width: 700px) {
  .contact__container {
    grid-template-columns: 1fr;
    padding: 0;
  }
  .title--contact-center {
    text-align: center;
  }
  .contact__content {
    width: 100%;
  }
}

.footer {
  margin-top: 50px;
  text-align: center;
  padding: 50px 0;
}
.footer__text {
  letter-spacing: 2px;
  font-size: 18px;
  font-weight: bold;
  color: var(--color-text);
}
.footer__content-social {
  margin-top: 30px;
}
.footer__icon {
  color: #1a3cab;
  margin: 0 15px;
}
@media (max-width: 700px) {
  .footer {
    margin-top: 20px;
  }
}

:root {
  --color-text: #414141;
  --color-primary: #284dc5;
}
* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}
.container {
  width: 85%;
  max-width: 90rem;
  margin: auto;
}

